#slides {
  width: 100%;
  height: 100%;
  display: block;
  padding: 0;
  overflow: hidden;
  //
  //&::after
  //  @include skewX(-20deg)
  //  @include transition(left .5s)
  //  content: ''
  //  display: block
  //  width: 50%
  //  height: 100%
  //  position: absolute
  //  top: 0
  //  left: 0
  //  background:
  //    color: rgba(255,255,255,.1)
  //
  //&:hover
  //  &::after
  //    left: 70%


  .slide {
    padding: 0 100px;

    &::after {
      @include skewX(20deg);
      @include transition(left .5s);
      content: '';
      display: block;
      width: 50%;
      height: 100%;
      position: absolute;
      top: 0;
      left: -10%;
      background: {
        color: rgba(255,255,255,.1); } }

    &:hover {
      &::after {
        left: 50%; } }

    .content {
      max-width: 600px;
      padding: 0 50px;
      margin: {
        top: 170px; }

      @media #{$small-max} {
        margin: {
          top: 0; }
        padding: 40px 0; }

      p {
        @extend %p;
        min-height: 105px;
        max-width: 500px;

        @media #{$small-max} {
          font: {
            size: 1em; } } }

      button {
        padding: 10px 20px;
        margin: 25px 0;
        display: table;

        @media #{$small-max} {
          display: block;
          text-align: center; } } }

    .image {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;

      &::before {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: {
          color: rgba(41, 50, 61, .6); } }

      img {
        width: 100%;
        max-height: 100%;
        display: block; } } }

  .prev,
  .next {
    @include transition(width .4s);
    position: absolute;
    overflow: hidden;
    cursor: pointer;
    opacity: .7;
    width: 40px;
    height: 35px;
    top: 0;
    bottom: 0;
    margin: auto;
    background: {
      image: url('../images/prev.png'); }

    &:hover {
      @include transition(width .4s);
      width: 65px; } }

  .prev {
    left: 20px;
    background: {
      image: url('../images/prev.png');
      position: left center; } }

  .next {
    right: 20px;
    background: {
      image: url('../images/next.png');
      position: right center; } }


  .buttons {
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 40px;
    z-index: 3;

    li {
      @include rounded(50%);
      position: relative;
      background: {
        color: $color-soft; }
      width: 12px;
      height: 12px;
      margin: 0 7px;
      display: inline-block;
      cursor: pointer;
      padding: 0;


      &:after {
        @include rounded(50%);
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        margin: auto;
        border: 1px solid $color-soft;
        padding: 0; }

      &.active,
      &:hover {
        &:after {
          @include animation(buttons .2s forwards); } } } } }


@keyframes buttons {
  80% {
    left: -15px;
    top: -15px;
    padding: 20px; }
  100% {
    left: -5px;
    top: -5px;
    padding: 10px; } }
